import { Controller, Commands } from '@ali/4ever-cangjie';
import getCanSetQuoteBlocks from '../utils/getCanSetQuoteBlocks';

export default function enableQuote(editor: Controller, next?) {
  const blocks = getCanSetQuoteBlocks(editor);
  blocks.forEach((block) => {
    editor.command(Commands.setNodeByKey, block.key, {
      type: block.type,
      data: {
        ...block.data,
        blockquote: true,
      },
    });
  });

  if (next) {
    next();
  }

  return editor;
}
